@import './partials/mixin';

body, #root{
    @include normal;
    background-image: url('./assets/images/main-bg.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-position-y: 15vh;
    width: 100vw;
    @media (max-width: 1315px) {
        background-position-y: 20vh;
    }
}